const initialState = {
    user: null,
    snackbar: "",
}

const userReducer = (state = initialState, action) => {
    /* console.log(action) */

    switch (action.type) {
        case 'user':
            return {
                ...state,
                user: action.payload,
            }
        
       case 'message':
            return {
                ...state,
                snackbar: action.payload,
            }
        
        default:
            return state
    }
}
export default userReducer